* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#top-nav {
  width: 100%;
  height: 80px;
  text-align: center;
}

#top-nav #back-button {
  float: left;
  margin-left: 20px;
  margin-top: 20px;
  width: 0px;
}

#top-nav button {
  background-color: transparent;
  border-top-left-radius: 100%;
  border-bottom-left-radius: 100%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  font-weight: bold;
  font-size: 20px;
  border: none;
  outline: none;
  width: 50px;
  color: #292121;
  cursor: pointer;
}

#top-nav button:hover {
  background-color: #fcfcfc;
  color: #f9c124;
}

#top-nav p {
  font-size: 20px;
  font-weight: bold;
}

#logo svg {
  padding: 5px 0px;
  margin-top: 10px;
}
